import styled from "styled-components";

export const HomeBackground = styled.div`
  height: 100vh;
  width: 100vw;

  perspective: 100px;
  overflow-x: hidden;
  overflow-y: auto;

  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;

  background: ${({ theme }) =>
    theme.name === "dark"
      ? "linear-gradient(180deg, #44475A 0%, #7678ED 100%)"
      : "linear-gradient(180deg, #FFB86C 0%, #FFFFFF 100%)"};
`;

export const HomeParallax = styled.img`
  position: absolute;
  bottom: 0;
  right: 0;
`;

export const GreetText = styled.p`
  position: absolute;
  font-size: 48px;
  font-weight: bold;
  color: ${({ theme }) => theme.colors.body};
  left: 332px;
  top: 240px;
`;

export const BodyText = styled.p`
  position: absolute;

  max-width: 700px;

  font-size: 36px;
  font-weight: bold;
  color: ${({ theme }) => theme.colors.body};
  left: 332px;
  top: 308px;
`;
