import React, { useState, useEffect } from "react";
import { HomeBackground, HomeParallax, GreetText, BodyText } from "./styles";

const Home = () => {
  const [offsetY, setOffsetY] = useState(window.pageYOffset);

  const handleScrollY = () => {
    const offset = window.pageYOffset;
    setOffsetY(offset);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScrollY);
  }, []);

  return (
    <HomeBackground>
      <HomeParallax
        style={{ bottom: offsetY * 1 }}
        src={require("../../images/Background/Sun.svg")}
        alt="sun"
      />
      <HomeParallax
        style={{ bottom: offsetY * 0.35 }}
        src={require("../../images/Background/BackMountain.svg")}
        alt="Back Mountain"
      />
      <HomeParallax
        style={{ bottom: offsetY * 0.15 }}
        src={require("../../images/Background/FifthMountain.svg")}
        alt="Fifth Mountain"
      />
      <GreetText>Hello, I'm Andr√©,</GreetText>
      <BodyText>
        a freelance software developer for web and embedded systems.
      </BodyText>
      <HomeParallax
        style={{ bottom: offsetY * 0.25 }}
        src={require("../../images/Background/FourthMountain.svg")}
        alt="Fourth Mountain"
      />
      <HomeParallax
        style={{ bottom: offsetY * 0.2 }}
        src={require("../../images/Background/ThirdMountain.svg")}
        alt="Third Mountain"
      />
      <HomeParallax
        style={{ bottom: offsetY * 0.15 }}
        src={require("../../images/Background/SecondMountain.svg")}
        alt="Second Mountain"
      />
      <HomeParallax
        src={require("../../images/Background/FrontBush.svg")}
        alt="Front Bush"
      />
      <HomeParallax
        style={{ bottom: offsetY * 0.75 }}
        src={require("../../images/Background/Birds.svg")}
        alt="Birds"
      />
    </HomeBackground>
  );
};

export default Home;
